const hitApi = (problem, attemptedAnswer, answer, currentDifficulty, units, st) => {
    
    const limit = queryParams.get('limit');
    const cid = queryParams.get('cid');
    const crcid = queryParams.get('crcid');
    const sid = queryParams.get('sid');
    const uid = queryParams.get('uid');
    const id = queryParams.get('id');
    $.ajax({
        url: "https://kicc.pk/iParhai/api.php?prb=" + problem + "&aa=" + attemptedAnswer + "&ca=" + answer + "&tt=" + 5 + "&st=" + st + "&tkn=" + tkn + "&limit=" + limit + "&cid=" + cid + "&crcid=" + crcid + "&sid=" + sid + "&dif=" + currentDifficulty + "&uid=" + uid + "&id=" + id + "&type=" + type + "&gt=" + "g",
        type: "post",
        data: "",
        success: function (data) {
            console.log(data)
            //alert("hit")
        },
        error: function (data) {
            //alert(data);
        }
    });

}
